<script lang='ts'>
	import { createEventDispatcher } from 'svelte';
	import { writable } from 'svelte/store';
	import Button from '../Button.svelte';

	export let isStroke = false;
	export let isStrokeW = writable(isStroke);
	export let icon = 'done';
	export let iconW = writable(icon);

	const dispatch = createEventDispatcher();

	let placeholder: HTMLDivElement;

	$: placeholder && (
		$isStrokeW
			? naivelyRecreateAsNewTag(placeholder, 'icono')
			: naivelyRecreateAsNewTag(placeholder, 'icon')
	);

	function naivelyRecreateAsNewTag(elem: HTMLElement, tag: string) {
		if (elem.tagName.toLowerCase() === tag.toLowerCase()) {
			return;
		}

		const newElement = document.createElement(tag);

		newElement.innerHTML = elem.innerHTML;

		elem.parentElement?.replaceChild(newElement, elem);
	}
</script>

<Button
	on:click={() => dispatch('click')}
	on:hover={() => dispatch('hover')}
	{...$$restProps}
>
	<placeholder
		slot='icon'
		bind:this={placeholder}
	>
		{$iconW}
	</placeholder>
	<slot></slot>
</Button>